import { Component, OnInit } from '@angular/core';
import { AlertController } from '@ionic/angular';
import { HttpClient } from '@angular/common/http';
import {ConfigService} from './../config.service';
import {ActivatedRoute} from '@angular/router';
@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss']
})
export class TabsPage implements OnInit {
  public param1: string;
  constructor(private host:ConfigService,public alertController: AlertController,public viewCtrl: PopoverController,private route: ActivatedRoute) {
    
  }
  ngOnInit() {
    this.route.queryParams.subscribe(params => {
        this.param1 = params['userID'];
    });
    if(this.param1){
      this.host.setHost(this.param1)
      
    }else{
      this.host.setHost("0")
      window.location.replace("")
    }
    
  }
  async viewPopover(){
    let modal = await this.viewCtrl.create({
      component: PopoverPage
    });
    modal.present();
  }
  async logout(){
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Confirmar',
      message: '<strong>Desea salir del sistema?</strong>',
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
            
          }
        }, {
          text: 'Aceptar',
          handler: () => {
            this.host.setHost("")
            window.location.replace("")
          }
        }
      ]
    });

    await alert.present();
  }
}

import { PopoverController } from '@ionic/angular';
@Component({
  template: `
  
    <ion-header>
      <ion-toolbar>
        <ion-title>
          Exportar
        </ion-title>
      </ion-toolbar>
    </ion-header>
    <ion-content>
      <form>
        <ion-item>
          <ion-label>Nombre:</ion-label>
          <ion-input type="text"  [(ngModel)]="Nombre" name="Nombre"></ion-input>
        </ion-item>
        
        <ion-item>
          <ion-label>Email:</ion-label>
          <ion-input type="email" [(ngModel)]="Email" name="Precio"></ion-input>
        </ion-item>
        
        <ion-item>
          <ion-label>Asunto:</ion-label>
          <ion-input type="text" [(ngModel)]="Asunto" name="Unidad"></ion-input>
        </ion-item>
        <ion-list  *ngFor="let exp of export">
          <ion-row>
              <ion-col>{{exp.Name}}</ion-col>
              
              <ion-col><ion-button  (click)=exportExcel(exp) color="success"><ion-icon name="download-outline"></ion-icon></ion-button></ion-col> 
              <ion-col><ion-button  (click)=sendEmail(exp) color="danger"><ion-icon name="mail-outline"></ion-icon></ion-button> </ion-col> 
          </ion-row>
        </ion-list> 
      </form>
    </ion-content>
  `
})
export class PopoverPage {
  public export;
  public Nombre;
  public Email;
  public Asunto;
  constructor(private http: HttpClient,private host:ConfigService,public viewCtrl: PopoverController) {
    this.Nombre="";
    this.Email ="";
    this.Asunto ="";
    this.http.get<any>(this.host.getHost() + '/viewSQL').subscribe(data => {
      this.export = data
    })
  }
  sendEmail(obj){
    var json = {
      "name":this.Nombre,
      "sql" : obj.Id,
      "email":this.Email,
      "asunto": this.Asunto
    }
    this.http.post<any>(this.host.getHost() + '/sendEmail',json).subscribe(data => {

    })
  }
  exportExcel(obj){
    var json = {
      "name":this.Nombre,
      "sql" : obj.Id,
      "email":this.Email,
      "asunto": this.Asunto
    }
    this.http.post<any>(this.host.getHost() + '/exportExcel',json).subscribe(data => {

    })
  }
  
  close() {
    this.viewCtrl.dismiss();
  }
}